.list-group-item{
	display: flex !important;
	justify-content: space-between;
	align-items: center;
	padding: .75rem 1.25rem .75rem 2.25rem !important;
}
.list-group-item .btn-group{
	margin-top: 0;
}

.form-control:disabled, .form-control[readonly] {
	background-color: transparent !important;
	padding: 0px;
	border:none;
	height: auto;
	height: initial;
}
.container{
	width: 50%;
	margin: 0 auto;
}
.todo-list {
	margin-top: 20px;
}

.form{
	margin: 0 auto 50px;
}
.btn-group{
	margin-top: 20px;
}
